* {
  user-select: none;
}

html, body {
  background: #fafafa;
  min-height: 100vh;
}

input:checked ~ svg {
  color: gray !important;
}

.optionsBox {
  width: 50%;
}

.spend-Input {
  margin: 0;
  display: block;
  width: 100%;
  padding: 8px;
  box-sizing: border-box;
  appearance: none;
  font-size: inherit;
  line-height: inherit;
  color: black;
  background-color: white;
  border: 1px solid;
  border-radius: 4px;
}

.cardListing {
  align-items: center;
  justify-content: start;
  flex-wrap: wrap;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  cursor: pointer;
  width: 400px;
  height: 250px;
  background: white;
}
.cardListing-Container {
  align-items: center;
  justify-content: center;
  width: 100%;
}
.cardListing-Image {
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3);
  margin-bottom: 5px;
  width: 150px;
}
.cardListing-Name {
  flex-grow: 1;
  text-align: right;
}
.cardListing-ROI {
  text-align: center;
  width: 100%;
}

@media only screen and (max-width: 800px) {
  .optionsBox {
    width: 100%;
  }

  .cardListing {
    justify-content: space-between;
  }
  .cardListing-Name {
    width: 50%;
  }
  .cardListing-ROI {
    text-align: center;
  }
}
